/* Farben:

  Primär: #27272a, #18181b,
  Sekundär: #f9fafb, #6b7280, 
  Akzent: #2dd4bf, #0f766e, #115e59, 
*/

/* Allgemein */
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: "Rubik", sans-serif;
  color: #f9fafb;
  background: radial-gradient(ellipse at top, #6b7280, #27272a);
  padding: 9.6rem 9.6rem 0;
  height: 100vh;
  overflow: hidden;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: "Varela Round", "Rubik", sans-serif;
}

p {
  font-size: 2rem;
  line-height: 1.4;
}

ul {
  list-style: none;
}

a:link,
a:visited {
  color: #2dd4bf;
  text-decoration: none;
  transition: 0.3s all;
}

a:hover,
a:active {
  color: #0f766e;
}

ion-icon {
  --ionicon-stroke-width: 7rem;
}

/*** Container - Hilfsklassen ***/
.container {
  max-width: 160rem;
  margin: 0 auto;
}

.grid {
  display: grid;
}

.grid-2-columns {
  grid-template-columns: repeat(2, 1fr);
}

.grid-3-columns {
  grid-template-columns: repeat(3, 1fr);
}

.grid-4-columns {
  grid-template-columns: repeat(4, 1fr);
}

.flex {
  display: flex;
}

.flex-column {
  flex-direction: column;
}

/*** Zustand - Hilfsklassen ***/
.hidden {
  display: none !important;
}

.loading {
  font-size: 2.4rem;
  font-style: italic;
  align-self: center;
  justify-self: center;
  font-weight: 500;
}

/*** Headings ***/
.primary-heading {
  font-size: 4.4rem;
  font-family: "Varela Round", "Rubik", sans-serif;
  font-weight: 500;
}

/*** Buttons ***/
.button {
  font-family: "Varela Round", "Rubik", sans-serif;
  font-size: 1.8rem;
  font-weight: 700;
  text-transform: uppercase;
  padding: 1.2rem 1.8rem;
  color: inherit;
  background: none;
  border: none;
  border-radius: 50px;
  transition: 0.3s all;
  cursor: pointer;
}

.button:hover {
  transform: scale(1.05);
}

.button-large {
  font-size: 2.4rem;
}
