/* Farben:

  Primär: #27272a, #18181b,
  Sekundär: #f9fafb, #6b7280, 
  Akzent: #2dd4bf, #0f766e, #115e59, 
*/

.main-container {
  gap: 8rem;
}

/*** Header ***/
.main-header .container {
  justify-content: space-between;
  align-items: center;
}

.new-button {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.8rem;
  background-color: #27272a;
}

.new-button ion-icon {
  padding-bottom: 0.2rem;
}

.main-content .container {
  grid-template-columns: 1fr 3fr;
  gap: 4.8rem;
}

/*** Form ***/
.create-form {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  gap: 2.4rem;
  align-items: center;
  background-color: #18181b;
  border-radius: 13px;
  padding: 2.4rem;
}

.create-form label {
  display: flex;
  align-items: center;
  position: relative;
}

/** Form Inputs **/
.create-form input,
.create-form textarea,
.create-form select {
  font-size: 1.8rem;
  color: inherit;
  padding: 1.2rem 1.6rem;
  background-color: #27272a;
  border: none;
  border-radius: 13px;
  width: 100%;
  padding-right: 5rem;
}

.create-form input::placeholder {
  color: #6b7280;
}

.create-form select {
  text-transform: uppercase;
}

.title-input {
  grid-column: 1 / -2;
  grid-row: 1 / 2;
}

.description-input {
  grid-column: 1 / -2;
  grid-row: 2 / 4;
}

.description-input textarea {
  border-radius: 13px;
}

.description-input textarea::placeholder {
  color: #6b7280;
}

.character-counter {
  font-size: 1.6rem;
  color: #6b7280;
  width: 3rem;
  text-align: right;
  position: absolute;
  right: 1rem;
}

/** Form Buttons **/
.create-form-buttons {
  display: flex;
  grid-column: -2 / -1;
  justify-self: end;
  gap: 2.4rem;
}

.create-button {
  box-shadow: 0px 0px 0px 2px #0f766e;
}

.create-button:hover {
  box-shadow: 0px 0px 0px 2px #115e59;
  background-color: #115e59;
}

.abort-button {
  box-shadow: 0px 0px 0px 2px #b91c1c;
}

.abort-button:hover {
  box-shadow: 0px 0px 0px 2px #991b1b;
  background-color: #991b1b;
}

/*** Filter ***/
.roadmap-filter-list {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
  background-color: #18181b;
  border-radius: 13px;
  padding: 4rem;
}

.roadmap-filter-list button {
  background: none;
  border: none;
  width: 100%;
  background-color: #27272a;
  font-size: 2rem;
  text-wrap: nowrap;
  box-shadow: none;
}

.roadmap-filter-list li:first-child button {
  color: #f9fafb;
  margin-bottom: 1.6rem;
}

/*** Roadmap Liste ***/
.roadmap-section {
  position: relative;
}

.roadmap-container {
  height: calc(100vh - 9.6rem - 5.3rem - 8rem);
  scrollbar-color: #6b7280 #ffffff00;
  scrollbar-width: thin;
  overflow-y: scroll;
}

.item-counter {
  position: absolute;
  top: -3rem;
  right: 0;
  text-align: right;
  font-size: 1.6rem;
  margin: 2.4rem;
  margin-top: 0;
}

.item-counter span {
  font-weight: 500;
}

.roadmap-list {
  display: flex;
  flex-direction: column;
  gap: 1.6rem;
}

.roadmap-list-item {
  display: flex;
  flex-direction: column;
  gap: 3.6rem;
  background-color: #18181b;
  padding: 2.4rem 3.2rem;
  border-radius: 13px;
  cursor: pointer;
}

/*** Roadmap Items ***/
.item-title-bar {
  display: flex;
  align-items: center;
  grid-column: 1 / -1;
  gap: 1.6rem;
  font-size: 2rem;
}

.item-title-bar p {
  font-size: inherit;
}

.modtype-tag {
  font-family: "Varela Round", "Rubik", sans-serif;
  font-size: 1.4rem;
  font-weight: 700;
  text-transform: uppercase;
  color: #27272a;
  padding: 0.35rem 0 0.25rem;
  border-radius: 13px;
  width: 11rem;
  text-align: center;
}

.modpage-link:link,
.modpage-link:visited {
  font-size: 1.8rem;
  text-wrap: nowrap;
}

/** Item Buttons **/
.item-button-container {
  display: flex;
  gap: 1.2rem;
  margin-left: auto;
}

.item-button-container button {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 3.2rem;
  width: 3.2rem;
  background-color: #6b7280;
  border: none;
  box-shadow: none;
  border-radius: 50%;
  transition: 0.3s all;
  padding: 0.5rem;
}

.item-button-container button:hover {
  background-color: #4b5563;
  box-shadow: none;
  transform: scale(1.1);
}

.item-button-container button:last-child {
  background-color: #b91c1c;
}

.item-button-container button:last-child:hover {
  background-color: #991b1b;
  box-shadow: none;
}

.icon-thin ion-icon {
  --ionicon-stroke-width: 4rem;
}

.icon-tiny ion-icon {
  font-size: 1.2rem;
  transform: rotate(-45deg);
}

.edit-button-icon {
  color: inherit;
}

/** Item Description **/
.item-description-container {
  display: none;
}

.item-description-container.active {
  display: block;
}

.item-description-text {
  line-height: 1.8;
  font-size: 1.6rem;
}

.item-description-release {
  text-align: right;
  margin-top: 1.6rem;
  font-size: 1.4rem;
}
