/* Farben:

  Primär: #27272a, #18181b,
  Sekundär: #f9fafb, #6b7280, 
  Akzent: #2dd4bf, #0f766e, #115e59, 
*/

/*** 1440px ***/
@media (max-width: 90em) {
  html {
    font-size: 56.25%;
  }

  .container {
    max-width: 128rem;
  }

  .primary-heading {
    font-size: 3.2rem;
  }

  .button {
    padding: 0.8rem 1.8rem;
    font-size: 2rem;
  }

  .loading {
    font-size: 2rem;
  }

  .main-container {
    gap: 6.4rem;
  }

  .create-form {
    gap: 2rem;
  }

  .main-content .container {
    gap: 3.6rem;
  }

  .create-form input,
  .create-form textarea,
  .create-form select {
    font-size: 1.4rem;
  }

  .character-counter {
    font-size: 1.2rem;
  }

  .create-form-buttons {
    gap: 1.6rem;
  }

  .create-form-buttons button {
    font-size: 1.6rem;
  }

  .roadmap-filter-list {
    gap: 2.2rem;
    padding: 2rem;
  }

  .roadmap-filter-list button {
    font-size: 1.4rem;
  }

  .roadmap-filter-list li:first-child button {
    margin-bottom: 0.5rem;
  }

  .item-counter {
    font-size: 1.2rem;
    top: -2.5rem;
  }

  .roadmap-list {
    gap: 1.4rem;
  }

  .roadmap-list-item {
    gap: 2.4rem;
    padding: 1.2rem 1.6rem;
  }

  .item-title-bar {
    gap: 1.2rem;
    font-size: 1.6rem;
  }

  .modtype-tag {
    font-size: 0.8rem;
    width: 8rem;
  }

  .modpage-link:link,
  .modpage-link:visited {
    font-size: 1.2rem;
  }

  .item-button-container {
    gap: 0.8rem;
  }

  .item-button-container button {
    height: 2.8rem;
    width: 2.8rem;
    padding: 0.5rem;
  }

  .item-description-text {
    line-height: 2;
    font-size: 1.4rem;
  }

  .item-description-release {
    font-size: 1.2rem;
  }
}

/*** 960px ***/
@media (max-width: 60em) {
  html {
    font-size: 50%;
  }

  body {
    padding: 6.4rem 0 0;
  }

  .container {
    max-width: 90vw;
  }

  .loading {
    font-size: 1.8rem;
    text-align: center;
  }

  .primary-heading {
    font-size: 4.4rem;
  }

  .main-header .container {
    flex-direction: column;
    gap: 4rem;
  }

  .main-content .container {
    grid-template-columns: 1fr;
    gap: 4.8rem;
  }

  .roadmap-filter-list li:first-child button {
    margin-bottom: 0;
  }

  .roadmap-filter-list {
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1.2rem;
    justify-content: left;
  }

  .item-title-bar {
    font-size: 2rem;
  }

  .item-description-text {
    line-height: 1.8;
    font-size: 1.8rem;
  }

  .item-description-release {
    margin-top: 2rem;
    font-size: 1.4rem;
  }

  .modtype-tag {
    font-size: 1.2rem;
    width: 10rem;
  }
}

/*** 720px ***/
@media (max-width: 45em) {
  .create-form {
    grid-template-columns: 1fr;
  }

  .create-form input,
  .create-form textarea,
  .create-form select {
    font-size: 1.8rem;
    line-height: 2;
  }

  .character-counter {
    font-size: 1.4rem;
  }

  .create-form-buttons {
    margin-top: 2.4rem;
    justify-self: center;
  }
}

/*** 480px ***/
@media (max-width: 30em) {
  html {
    font-size: 43.75%;
  }

  .container {
    max-width: 92.5vw;
  }

  .loading {
    padding: 0 2.4rem;
  }

  .button {
    padding: 1.4rem 2.8rem;
    font-size: 2.4rem;
  }

  .roadmap-filter-list {
    flex-direction: column;
    flex-wrap: wrap;
    gap: 2rem;
    padding: 2rem;
  }

  .roadmap-filter-list button {
    padding: 1.4rem 0;
    font-size: 2rem;
  }

  .item-title-bar {
    font-size: 2.4rem;
    flex-direction: column;
    gap: 4rem;
    text-align: center;
  }

  .item-button-container {
    margin-left: 0;
    gap: 2rem;
  }

  .item-button-container button {
    height: 4rem;
    width: 4rem;
  }

  .item-description-text {
    font-size: 2rem;
  }

  .modtype-tag {
    font-size: 1.6rem;
    width: 12rem;
  }

  .modpage-link:link,
  .modpage-link:visited {
    font-size: 1.6rem;
  }

  .item-description-text {
    margin-top: 3.2rem;
  }

  .item-description-release {
    margin-top: 4rem;
  }
}
